/**
 * 
 */
export var dateFormatter = function(date, time) {
	var y = date.getFullYear();  
    var m = date.getMonth() + 1;  
    m = m < 10 ? '0' + m : m;  
    var d = date.getDate();  
    d = d < 10 ? ('0' + d) : d;
    if (time !== undefined) {
        var hours = date.getHours();
        hours = hours < 10 ? ('0' + hours) : hours;
        var minute = date.getMinutes();
        minute = minute < 10 ? ('0' + minute) : minute;
        var second = date.getSeconds();
        second = second < 10 ? ('0' + second) : second;
        return y + '-' + m + '-' + d + ' ' + hours + ':' + minute + ':' + second; 
    }
    return y + '-' + m + '-' + d; 
}

/**
 * 比较两个日期的大小
 */
export var dateCompare = function(date1, date2) {
	var date1 = new Date(date1); 
	var date2 = new Date(date2); 
    if (date1 > date2) {
    	return true;
    } else {
    	return false;
    }
}
/**
 * 时间戳转YYYY-MM-DD
 */
export var timeStempToDate = function(stemp) {
	var date = new Date(stemp);
	var mydate = dateFormatter(date);
	return mydate;
}
/**
 * 时间戳转YYYY-MM-DD HH:MM:SS
 */
export var timeStempToDatetime = function(stemp) {
    var date = new Date(stemp);
    var mydate = dateFormatter(date, 1);
    return mydate;
}


