<template>
	<div class="UserControl">
	<!-- 搜索信息 -->
	   <el-row :gutter="20">
		  <el-col :xs="3" :sm="3" :md="4"><el-input v-model="seach.name" placeholder="姓名搜索"></el-input></el-col>
		  <el-col :xs="3" :sm="3" :md="4"><el-input v-model="seach.email" placeholder="邮箱搜索"></el-input></el-col>
		  <el-col :xs="4" :sm="3" :md="4">
		    <el-select v-model="seach.departmentId" placeholder="选择职能部门">
		      <!-- <el-option label="选择职能部门" value="选择职能部门"></el-option> -->
		      <el-option label="全部" value="0"></el-option>
		      <el-option label="市场部" value="7"></el-option>
		      <el-option label="运营部" value="5"></el-option>
		      <el-option label="技术部" value="6"></el-option>
		    </el-select>
		  </el-col>
		  <el-col :xs="4" :sm="3" :md="4">
		  	<el-select v-model="seach.level" placeholder="选择账号等级">
		      <!-- <el-option label="选择账号等级" value="选择账号等级"></el-option> -->
		      <el-option label="全部" value="0"></el-option>
		      <el-option label="一级账号" value="1"></el-option>
		      <el-option label="二级账号" value="2"></el-option>
		      <el-option label="三级账号" value="3"></el-option>
		    </el-select>
		  </el-col>
		   <el-col :xs="10" :sm="8" :md="6" >
		   	 <el-button  type="primary" icon="el-icon-search" @click="secrch" size="small"></el-button>
		   	 <el-button  type="success" icon="el-icon-plus" @click="addUser" size="small" v-show="isShow"></el-button>
		   </el-col>
		</el-row>
		<!-- 表格 -->
		<div class="table-wrapper">
			<el-table :data="fbsData" style="width: 90%" border size="small"  v-loading="loading" element-loading-text="拼命加载中...">
                <el-table-column prop="name" label="姓名" width='80'></el-table-column>
                <el-table-column prop="email" label="邮箱" width="200"></el-table-column>
                <el-table-column prop="departmentId" label="职能部门"  ></el-table-column>
                <el-table-column prop="level" label="账号等级"></el-table-column>
                <el-table-column prop="date" label="创建时间" width="150"></el-table-column>
               <!--  <el-table-column prop="showTitle" label="权限管理" v-if="isShow">
		            <template slot-scope="scope">
		                <el-button close-transition @click="adminuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow">管理</el-button>
		            </template>   
		      </el-table-column> -->
               <!--  <el-table-column label="账号管理" width="100" v-if="isShow">
                    <template slot-scope="scope">
                        <el-button @click="readonlyuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow && scope.row.state == 0"><i class="fa fa-ban"></i></el-button>
                        <el-button @click="readonlyuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow && scope.row.state != 0"><i class="fa fa-ban staylcolor"></i></el-button>
                        <el-button @click="deleteuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow"><i class="el-icon-delete"></i></el-button>
                    </template>
                </el-table-column> -->
            </el-table>
            <el-pagination
                background
                @size-change="pageSizeChange"
                @current-change="currentPageChange"
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="dataSize" class="paginationWrapper" v-show="tableshow">
            </el-pagination>
		</div>
		<!-- 模态框 -->
		<el-dialog title="创建新用户" :visible.sync="dialogVisible" width="50%" >
            <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
			  <el-form-item label="邮箱(账号)" prop="email">
			    <el-input v-model="ruleForm.email"></el-input>
			  </el-form-item>
			  <el-form-item label="密码" prop="pwd">
			    <el-input v-model="ruleForm.pwd"></el-input>
			  </el-form-item>
			  <el-form-item label="姓名" prop="name">
			    <el-input v-model="ruleForm.name"></el-input>
			  </el-form-item>
			  <el-form-item label="职能部门" prop="departmentId">
			    <el-select v-model="ruleForm.departmentId" placeholder="选择职能部门">
			      <div v-for="item in userdepartment">
			          <el-option :label="item.name" :value="item.id"></el-option>
			     </div>
			    </el-select>
			  </el-form-item>
			  <el-form-item label="账号级别" prop="level">
			    <el-select v-model="ruleForm.level" placeholder="选择账号级别">
			      <div v-for="item in userlevel">
			          <el-option :label="item.name" :value="item.id"></el-option>
			      </div>
			    </el-select>
			  </el-form-item>
			  <el-form-item label="上司" prop="higherId">
			    <el-select v-model="ruleForm.higherId" placeholder="默认创建者">
			    	<div v-for="item in superior">
			             <el-option :label="item.name" :value="item.id"></el-option>
			    	</div>
			      <!-- <el-option label="区域二" value="beijing"></el-option> -->
			    </el-select>
			  </el-form-item>
			   <el-form-item>
			    <el-button type="primary" @click="submitForm('ruleForm')" :loading="addSaveLoading">保存</el-button>
			    <el-button @click="dialogVisible = false">取消</el-button>
			  </el-form-item>
			</el-form>
		</el-dialog>
	   <!-- 模态框 -->
	</div>
</template>
<script>
import {getAdminList,addAdmin,AdminParameter} from '../../api/api.js'
	export default{
		data(){
			return{
				// 是否显示添加
				isShow: true,
				// 分页是否显示
				tableshow: false,
				loading: false,
				// 是否用户权限值
				userpermission:null,
				// 用户信息
				userperadmin:{},
				dialogVisible: false,
				// 账号级别
				userlevel:[],
				// 用户上司
				superior:[],
				// 职能部门
				userdepartment:[],
				// 搜索对象信息
				seach:{
				  sessionid:'',
                  name: '',
				  email: '',
				  departmentId:'',
				  level:'',
				  pageId:0,
				  pageSize:10,
				  first:0
				},
				// 分页信息
				currentPage: 1,
			    pageSizes: [10,20,50],
			    dataSize: 0,
			    pagesize: 10,
			    // 获取数据
				fbsData:[
				  
				],
				// 数据为空时验证信息
                 ruleForm: {
                  sessionid:'',
		          email:'',
		          pwd: '',
		          name:'',
		          departmentId:'',
		          level: '',
		          higherId:''

		        },
		        addSaveLoading:false
			}
		},
		mounted(){
			// 获取数据信息
			// this.getUserInfo();
			this.seach.sessionid = parseInt(localStorage.getItem("userId"));
			this.userpermission = parseInt(localStorage.getItem("userpermission"));
			var userperadmin = localStorage.getItem("userperadmin");
			this.userperadmin = JSON.parse(userperadmin);
			// console.log(this.userperadmin.sessionid)
			this.ruleForm.sessionid = this.userperadmin.sessionid;
			if (this.userpermission >2) {
                this.isShow = false;
			} else {
				this.isShow = true;
			}
			this.secrch();
		},
		 methods: {
		 	// 搜索
		  secrch(){
		  	console.log(this.seach)
		  	if (this.seach.email) {
		  		var reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/; 
                if (!reg.test(this.seach.email)) {
                    this.$message.error('输入邮箱格式不正确');
                    return;
                }
		  	}
		  	if (this.seach.departmentId === '0') {
		  		this.seach.departmentId = "";
		  	} else if (this.seach.level === '0') {
		  		this.seach.level = "";
		  	}
		  	this.loading = true;
		  	let seach = JSON.stringify(this.seach)
		  	console.log(seach)
		  	getAdminList(seach).then((res)=>{
		  		console.log(res.data)
		  		if (res.data.status === 0) {
		  			// 获取数据信息
		  			this.fbsData = res.data.adminList;
		  			if (this.fbsData.length<1) {
		  				this.loading = false;
                        // 显示分页
			  			this.tableshow = false;
		  			} else {
		  				for (var i = 0; i <this.fbsData.length; i++) {
			  				// 时间戳转时间
							this.fbsData[i].date = this.formatDate(this.fbsData[i].date);
			  				if (this.fbsData[i].departmentId === 5) {
			  					this.fbsData[i].departmentId = "运营部"
			  				} else if (this.fbsData[i].departmentId === 6) {
			  					this.fbsData[i].departmentId = "技术部"
			  				} else if (this.fbsData[i].departmentId === 7) {
			  					this.fbsData[i].departmentId = "市场部"
			  				};
			  				if (this.fbsData[i].level === 1) {
			  					this.fbsData[i].level = '一级账号'
			  				} else if (this.fbsData[i].level === 2) {
			  					this.fbsData[i].level = '二级账号'
			  				} else if (this.fbsData[i].level === 3) {
			  					this.fbsData[i].level = '三级账号'
			  				};
			  			}
			  			if (!res.data.listSize) {
			  				this.dataSize = this.dataSize;
			  			} else {
			  			   this.dataSize= parseFloat(res.data.listSize);
			  			}
			  			this.loading = false;
			  			// 显示分页
			  			this.tableshow = true;
			  		}	
		  		} else if (res.data.status === 1) {
		  		   this.tableshow = false;
		  		   this.$router.replace("/login");
		  		}
		  	}).catch(res=>{
		  		this.loading = false;
		  		this.$message.error('数据拉取失败');
		  		console.log(res)
		  	})
		  },
		  // 添加用户
		  addUser(){
		  	// 点击添加时候情况输入框
		        this.ruleForm.email = '';
		        this.ruleForm.pwd = '';
		        this.ruleForm.name = '';
		        this.ruleForm.departmentId = '';
		        this.ruleForm.level = '';
		        this.ruleForm.higherId = '';
		    // 职能部门数据
		  	this.userdepartment = [];
		  	if (this.userperadmin.admin.departmentId === 5) {
				this.userdepartment.push({'name':'运营部','id':5});
			} else if (this.userperadmin.admin.departmentId === 6) {
				this.userdepartment.push({'name':'技术部','id':6});
			} else if (this.userperadmin.admin.departmentId === 7) {
				this.userdepartment.push({'name':'市场部','id':7});
			};
			// 账号级别
			this.userlevel = [];
		  	console.log(this.userperadmin.admin.level)
		  	if (this.userperadmin.admin.level === 1) {
		  		this.userlevel = [{'name':'二级账号','id':2},{'name':'三级账号','id':3}];
		  	} else  {
		  		this.userlevel = [{'name':'三级账号','id':3}];
		  	};
		  	var data = {
		  		sessionid : this.userperadmin.sessionid
		  	};
		  	data = JSON.stringify(data);
		  	AdminParameter(data).then(res=>{
                console.log(res.data)
                if (res.data.status === 0) {
                	this.superior = res.data.adminList;
                	console.log(this.superior)
                	this.dialogVisible = true;
                }
                if (res.data.status === 1) {
                  this.$router.replace("/login");
                }
		  	}).catch(res=>{
		  		this.$message.error('获取负责人信息失败');
		  	})
		  	// this.userperadmin;
		  	// this.dialogVisible = true;
		  },
		  pageSizeChange(res){
		  	this.seach.pageSize = res;
		  	this.seach.first = 1;
		  	this.secrch()
		  },
		  currentPageChange(res){
		  	this.seach.pageId = (res*1-1);
		  	this.seach.first = 1;
		  	this.secrch()
		  },
		  // 管理事件
		  adminuser(data,index){
		  	console.log(data)
		  	console.log(index)
		  },
		  // 冻结用户
		  readonlyuser(data,index){
		  	console.log(data)
		  	console.log(index)
		  },
		  // 删除用户
		  deleteuser(data,index){},
	      submitForm(formName) {
	        this.$refs[formName].validate((valid) => {
	          if (valid) {
	            console.log(this.ruleForm)
	            if (this.ruleForm.email === '') {
	            	this.$message.error('邮箱不能为空');
	            	
	            } else if (this.ruleForm.pwd === '') {
	            	this.$message.error('密码不能为空');
	            } else if (this.ruleForm.name === '') {
	            	this.$message.error('姓名不能为空');
	            } 
	            else if (this.ruleForm.departmentId === '') {
	            	this.$message.error('职能部门不能为空');
	            } else if (this.ruleForm.level === '') {
	            	this.$message.error('账号级别不能为空');
	            } 
	            else if (this.ruleForm.higherId === '') {
	            	this.$message.error('上司不能为空');
	            } else {
	            	if (this.ruleForm.email) {
	            	    var reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/; 
		                if (!reg.test(this.ruleForm.email)) {
		                    this.$message.error('输入邮箱格式不正确');
		                    return;
		                } else {
		                	this.addSaveLoading = true;
		                	var _this = this;
		                	let ruleForm = JSON.stringify(_this.ruleForm)
		                	addAdmin(ruleForm).then(res=>{
                               console.log(res.data)
                               if (res.data.status === 0) {
                               	   _this.$message({
							          message: res.data.statusMsg,
							          type: 'success'
							        });
                               	   _this.addSaveLoading = false;
                               	   _this.dialogVisible = false;
                               	   _this.secrch();
                               }
                               if (res.data.status === 1) {
                               	 _this.$router.replace("/login");
                               	 _this.addSaveLoading = false;
                               	 _this.dialogVisible = false;
                               }
		                	}).catch(res=>{
                               _this.addSaveLoading = false;
                               _this.dialogVisible = false;
                               _this.$message.error('保存失败');
		                	})
		                }
	            	}
	            }
	          } else {
	            console.log('error submit!!');
	            return false;
	          }
	        });
	      }
	    }
	}
</script>
<style lang="scss" type="text/css" scoped>
   .UserControl{margin-top: 1.25rem;}
   .table-wrapper{margin-top: 1.5rem;}
   .staylcolor{color:red}
</style>


