			// 转换权限
			var channelPermission = this.chartPermissionInfo;
			var hasPermission = this.permissionFormatter(channelPermission, 3);
			channelPermission = this.permissionFormatter(channelPermission, 2);
			if(!hasPermission){
				this.toast('请选择账户权限', 'warning');
		        return;
			}
			parameter = this.actionCompanyInfo;
			parameter['channelPermission'] = channelPermission;
			if(this.show === 2){
				// 新增渠道商操作
				parameter['sessionid'] = this.userperadmin.sessionid;
			}
			if(this.show === 3) {
				// 修改渠道商操作
				parameter['id'] = this.editId;
				parameter['channelCou'] = this.channelCou;
				parameter['permissionId'] = this.permissionId;
				parameter['createTime'] = this.createTime;
