		permissionFormatter(permissionList, type) {
			/**
			 * 权限转换
			 * type = 1 表示数值转布尔， = 2表示布尔转数值, 3表示计算true值的个数
			 */
			var keys = Object.keys(permissionList);
			var values = Object.values(permissionList);
			if(type === 1){
				values.forEach(function(item,index){
					if(keys[index] !== 'id'){
						if(item === 1){
							permissionList[keys[index]] = true;
						}else{
							permissionList[keys[index]] = false;
						}
					}
				});
			}else if(type === 2){
				values.forEach(function(item,index){
					if(keys[index] !== 'id'){
						if(item === true){
							permissionList[keys[index]] = 1;
						}else{
							permissionList[keys[index]] = 0;
						}
					}
				});
			}else if(type === 3){
				return values.includes(true);
			}else {
				// 重置
				var _this = this;
				values.forEach(function(item,index){
					if(keys[index] !== 'id'){
						_this.chartPermissionInfo[keys[index]] = false;
					}else {
						_this.chartPermissionInfo[keys[index]] = '';
					}
				});
			}
			return permissionList;
		},
