	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning',
	          center: true
	        }).then(() => {
	        	this.toast('删除成功!', 'success');
	        }).catch(() => {
	        	this.toast('已取消删除!', 'info');
	        });        
		},
