		getChannelParams() {
			var parameter1 = {
				sessionid: this.userperadmin.sessionid
			};
			console.log(parameter1);
			this.loading2 = true;
			getChannelParameterReg(parameter1).then((res)=>{
		  		console.log(res);
		  		this.loading2 = false;
		  		if(res.data.status === 0) {
		  			this.channelInfo.channelList = res.data.channelNameList;
		  			this.channelInfo.channelNoList = res.data.channelNoList;
		  			if(res.data.adminIdList && res.data.adminIdList.length !== 0){
		  				var _this = this;
		  				_this.channelInfo.channelManList = [];
