		saveChannelInfo() {
			/**
			 * 保存渠道信息
			 */
			if(this.loading5) return;
			if(this.actionChannelInfo.channelName === ''){
				this.toast('渠道名称不能为空', 'warning');
		        return;
			}
			if(this.actionValidate.channelNo){
				this.toast('渠道号不能为空', 'warning');
		        return;
			}
			if(this.appInfo.appSelected === ''){
				this.toast('所属APP不能为空', 'warning');
		        return;
			}
			if(this.channelAttributeInfo.attributeSelected === ''){
				this.toast('渠道属性不能为空', 'warning');
		        return;
			}
			if(this.channelTypeInfo.typeSelected === ''){
				this.toast('渠道类型不能为空', 'warning');
		        return;
			}
			if(this.channelDivisionInfo.divisionSelected === ''){
				this.toast('渠道细分不能为空', 'warning');
		        return;
			}
			if(this.fcType.isStep === 0 && this.fcType.stepData === ''){
				this.toast('分成单价(分成比例)不能为空', 'warning');
		        return;
			}
			if(this.fcType.isStep === 1){
				var values = Object.values(this.fcTypeData.rewards);
				if(values.includes(0) && values.includes('') && this.fcTypeData.last.price === 0 && this.fcTypeData.last.price === ''){
					this.toast('分成单价(分成比例|首提人数|首提金额|注册用户数)不能为空', 'warning');
			        return;
				}
			}
			if(this.settlementCycleInfo.typeSelected === ''){
				this.toast('结算周期不能为空','warning');
		        return;
			}
			if(this.cacCostTypeInfo.typeSelected === ''){
				this.toast('成本计算方式不能为空','warning');
		        return;
			}
			if(this.manInfo.manSelected === ''){
				this.toast('负责人不能为空','warning');
		        return;
			}
			this.actionChannelInfo.rewards = [];
			if(this.fcType.isStep === 0){
				var aReward = {};
				aReward.price = this.fcType.stepData;
				aReward.order = 1;
				aReward.typeId = this.rewardTypeInfo.typeSelected;
				aReward.adminId = this.manInfo.manSelected;
				aReward.updateAdminId = this.userperadmin.admin.id;
				aReward.costing = this.cacCostTypeInfo.typeSelected;
				aReward.settlementCycle = this.settlementCycleInfo.typeSelected;
				if(this.channelInfo.show === 3){
					aReward.id = this.editRewards[0].id;
					aReward.channelId = this.editRewards[0].channelId;
					aReward.date = this.editRewards[0].date;
				}
				this.actionChannelInfo.rewards.push(aReward);
			}else {
				this.actionChannelInfo.rewards = this.fcTypeData.rewards.slice();
				this.actionChannelInfo.rewards.push(this.fcTypeData.last);
				var _this = this;
				var datetime = new Date();
				datetime = dateFormatter(datetime, 1)
				this.actionChannelInfo.rewards.forEach(function(item,index){
					item.order = index + 1;
					item.typeId = _this.rewardTypeInfo.typeSelected;
					item.adminId = _this.manInfo.manSelected;
					item.updateAdminId = _this.userperadmin.admin.id;
					item.costing = _this.cacCostTypeInfo.typeSelected;
					item.settlementCycle = _this.settlementCycleInfo.typeSelected;
					if(_this.channelInfo.show === 3){
						item.id = _this.editRewards[0].id;
						item.channelId = _this.editRewards[0].channelId;
						item.date = _this.editRewards[0].date;
					}
				});
			}
			
			var parameter = {
				sessionid: this.userperadmin.sessionid,
				proxyId: this.companyInfo.id,  // 渠道商id
				//higherChannelId: '', // 上级渠道id
				level: (this.actionChannelInfo.level === '二级渠道') ? 2 : 1,  // 渠道级别
				channelName: this.actionChannelInfo.channelName, // 渠道名称
				channelNamePY: '', // 渠道名称全拼
				channelNo: this.actionChannelInfo.channelNo,// 渠道号
				channel: this.actionChannelInfo.channel,// 渠道编码
				isCreate: (this.actionChannelInfo.level === '二级渠道') ? 0 : this.actionChannelInfo.isCreate, // 1：可创建，0：不可创建
