				attribute: this.channelAttributeInfo.attributeSelected, // 渠道属性
				type: this.channelTypeInfo.typeSelected, // 渠道类型
				subdivision: this.channelDivisionInfo.divisionSelected, // 渠道细分
				url: this.actionChannelInfo.url, // H5链接
				//rewardId: '', // 分成方案id
				adminId: this.manInfo.manSelected, // 负责人ID
				permissionId: this.permissionId, // 
				rewards: this.actionChannelInfo.rewards
			}
			if(this.channelInfo.show === 3){
				parameter.id = this.editChannelId;
				parameter.rewardId = this.editRewardId;
			}
