						// 二级渠道的渠道编码生成
						this.actionChannelInfo.channel = this.currentFirstLevelChannelNo + '_' + this.actionChannelInfo.channelNo;
					}
					// 每次失去焦点都重新设置推广链接的值
					var url = this.actionChannelInfo.url.split('=');
					if(this.actionChannelInfo.channelNo === ''){
						this.actionChannelInfo.url = url[0] + '=';
					}else{
						this.actionChannelInfo.url = url[0] + '=' + this.actionChannelInfo.channel;
