		attributeChange(val) {
			/**
			 * 渠道属性选择变化
			 */
			var _this = this;
			_this.channelTypeInfo.typeList = [];
			_this.channelTypeInfo.typeSelected = '';
			this.channelTypeInfo.typeListAll.forEach(function(item,index){
				if (item.higherId === val){
					_this.channelTypeInfo.typeList.push(item);
				}
			});
		},
		typeChange(val) {
			/**
			 * 渠道类型选择变化
			 */
			var _this = this;
			_this.channelDivisionInfo.divisionList = [];
			_this.channelDivisionInfo.divisionSelected = '';
			this.channelDivisionInfo.divisionListAll.forEach(function(item,index){
				if (item.higherId === val){
					_this.channelDivisionInfo.divisionList.push(item);
				}
			});
		},
		rewardTypeChange(val) {
			/**
			 * 分成方式选择变化
			 */
			console.log(val)
			if(val === 26){
				this.fcType.text = '分成单价';
				this.fcType.text1 = '注册用户';
				this.fcType.unitText = '元';
			}else if(val === 27){
				this.fcType.text = '分成单价';
				this.fcType.text1 = '首提用户';
				this.fcType.unitText = '元';

			}else{
				this.fcType.text = '分成比例';
				this.fcType.text1 = '首提金额';
				this.fcType.unitText = '%';
			}
		},
		addFcItem() {
			/**
			 * 增加一条分成数据
			 */
			var currentdataLen = this.fcTypeData.length;
			var item = {
				max: 0,
				price: 0
			};
			this.fcTypeData.rewards.push(item);
			this.fcTypeData.length++;
		},
		minusFcItem() {
			/**
			 * 删除一条分成数据
			 */
			this.fcTypeData.rewards.pop();
			this.fcTypeData.length--;
		},
		setNextMinValue(index) {
			console.log(index);
			var prevMax = parseInt(this.fcTypeData.rewards[index].max) + 1;
			this.fcTypeData.mins[index+1] = prevMax;
			console.log(this.fcTypeData.rewards);
			console.log(this.fcTypeData.mins);
		},
		toast(msg, type) {
			this.$message({
	            type: type,
	            message: msg
	        });
		}
	}
}
</script>
<style scoped lang="scss">
li{list-style: none;}
.clearfix{
	zoom: 1;
}
.clearfix:after{
	content: "";
	display: block;
	clear: both;
	overflow: hidden;
	text-overflow: visibility;
}
button{
	outline: none;
	cursor: pointer;
}
input[readonly=readonly],
input[readonly=true],
textarea[readonly=readonly],
textarea[readonly=true]{
	background: #f1f1f1;
	color: #666;
	outline: none;
}
.actions{
	border-bottom: 4px solid #DCDFE6;
	padding-bottom: 1rem;
	margin-bottom: 1rem;
	.btns{
		text-align: right;
	}
	.btn{
		padding: .3rem .8rem;
		color: #fff;
		font-size: .7rem;
		margin-right: .4rem;
		-webkit-border-radius: .1rem;
		-moz-border-radius: .1rem;
		border-radius: .1rem;
	}
	.blue{
		background: #409EFF;
	}
	.green{
		background: #67C23A;
	}
}
.btns{
	text-align: right;
}

.breadnav{
	font-size: .5rem;
	margin-bottom: .8rem;
	border-left: 4px solid #324057;
	padding-left: .3rem;
	color: #999;
	font-weight: normal;
	a{
		color: #409EFF;
	}
}
.table-title{
	font-size: .6rem;
	margin-bottom: .5rem;
	span{
		color: #666;
		font-weight: bold;
	}
}
.action-wrapper{
	h4{
		text-align: center;
		font-size: .8rem;
		font-weight: normal;
		margin-bottom: 1rem;
	}
	.form-wrapper{
		width: 100%;
		margin: 0 auto;
		.input-group{
			font-size: 0;
			padding: .2rem 0;
			padding-right: 10%;
			&>label{
				display: inline-block;
				width: 30%;
				height: 100%;
				text-align: right;
				font-size: .6rem;
				padding-right: 2%;
				box-sizing: border-box;
				white-spacing: nowrap;
			}
			&>input{
				padding: .5rem 0;
				width: 60%;
				box-sizing: border-box;
				border-radius: .1rem;
				border: 1px solid #DCDFE6;
				padding-left: 2%;
				&.error{
					border-color: red;
				}
			}
			.date-input{
				width: auto;
			}
			.inner-input-group{
				width: 70%;
				float: right;
				ul{
					width: 100%;
					li{
						width: 100%;
						padding: .2rem 0;
						div:first-child{
							margin-bottom: .2rem;
						}
						span{
							display: inline-block;
							font-size: .5rem;
							color: #666;
							&.seperate{
								padding: 0 .2rem;
							}
							&.unit{
								color: red;
								padding-left: .2rem;
							}
						}
						input{
							width: 24%;
							padding: .4rem;
							border: 1px solid #DCDFE6;
							border-radius: .1rem;
						}
					}
				}
				button{
					padding: .3rem 1rem;
					border: none;
					background: #409EFF;
					color: #fff;
					border-radius: .1rem;
					&:last-child{
						margin-left: .4rem;
					}
				}
			}
		}
		.textAreaInput{
			font-size: 0;
			height: 3rem;
			line-height: 1.6rem;
			padding-right: 10%;
			label{
				display: inline-block;
				width: 30%;
				height: auto;
				text-align: right;
				font-size: .6rem;
				padding-right: 2%;
				box-sizing: border-box;
				white-spacing: nowrap;
				vertical-align: top;
			}
			textarea{
				height: 100%;
				width: 60%;
				box-sizing: border-box;
				border-radius: .1rem;
				border: 1px solid #DCDFE6;
				padding-left: 2%;
				resize: none;
			}
		}
		.required{
			label{
				position: relative;
			}
			&>label:first-child{
				&:after{
					content: "*";
					display: inline-block;
					color: red;
					font-weight: bold;
				}
			}
		}
	}
	.permission-wrapper{
		.form-wrapper{
			width: 86%;
			margin: 0 auto;
		}
		.input-group{
			label{
				margin-bottom: 1rem;
				text-align: left;
			}
		}
	}
	.submit-btns{
		text-align: center;
		margin-top: 2rem;
		font-size: 0;
		.btn{
			width: 4rem;
			height: 1.6rem;
			font-size: .6rem;
			margin-right: 1rem;
			border: none;
			color: #fff;
			border-radius: .1rem;
			&:last-child{
				margin-right: 0;
			}
		}
		.submit{
			background-color: #409EFF;
		}
		.cancel{
			background-color: #DCDFE6;
			color: #999;
		}
	}
}
.landscape{
	.action-wrapper{
		.form-wrapper{
			width: 100%;
			.input-group{
				font-size: 0;
				padding: .2rem 0;
				font-size: .4rem;
				overflow: hidden;
				label{
					width: 26%;
					float: left;
					padding: .6rem 0;
				}
				input{
					float: right;
					width: 74%;
					padding: .6rem 0;
				}
				.date-input{
					width: 74%;
				}
				.el-select{
					width: 74%;
				}
				textarea{
					width: 74%;
					height: 3rem;
				}
			}
			.inner-input-group{
				width: 70%;
