						width: 100%;
						padding: .2rem 0;
						span{
							display: inline-block;
							font-size: .5rem;
							color: #666;
							&.seperate{
								padding: 0 .2rem;
							}
							&.unit{
								color: red;
								padding-left: .2rem;
							}
							&.text{
								width: 100%;
								padding: .4rem 0;
							}
						}
						input{
							float: none;
							width: 34%;
						}
					}
				}
				button{
					padding: .3rem 1rem;
					border: none;
					background: #409EFF;
					color: #fff;
					border-radius: .1rem;
				}
			}
		}
	}
}

.paginationWrapper{
	text-align: center;
	margin-top: 10px;
	margin-bottom: 20px;
}
</style>


