<template>
	<div class="channelclass" v-loading="loading" element-loading-text="拼命加载中...">
		<div class="addfrist">
			<el-button type="success" icon="el-icon-plus" size='mini' @click='appendFirst'></el-button>
		</div>
		<div class="tree">
			<el-tree
			  :data="data4"
			  :props="defaultProps"
			  default-expand-all
			  node-key="id"
			  :expand-on-click-node="false"
			  :render-content="renderContent">
			</el-tree>
			<el-dialog
			  :title="modeTitle"
			  :visible.sync="dialogVisible"
			  width="30%">
			  <el-input v-model="addInfo.name" placeholder="请输入添加名称内容"></el-input>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="dialogVisible = false">取 消</el-button>
			    <el-button type="primary" @click="saveAddInfo" :loading="savelog">保存</el-button>
			  </span>
			</el-dialog>
		</div>
	</div>
</template>
<script>
import {getChannelAttribute,saveChannelAttribute} from '../../api/api.js'
  export default {
    data() {
      return {
      	// 第一次使用获取数据时候传sessionid对象
      	sessionidobj:{},
      	// 保存加载时候加载log
      	savelog:false,
      	// 模态框title
      	modeTitle:'',
      	// 添加保存对象
      	addInfo:{},
      	// 创建者
      	creatAdminId:'',
      	// 创建内容
      	name:'',
      	dialogVisible: false,
      	loading:true,
        data4: [
        {
          id: 1,
          label: '免费流量',
          children: [{
            id: 3,
            label: 'BD',
            children: [{
              id: 7,
              label: '甲方合作'
            }]
          },{
            id: 10,
            label: '市场',
            children: [{
              id: 7,
              label: '应用市场'
            }]
          }]
        }, {
          id: 2,
          label: '付费流量',
          children: [{
            id: 5,
            label: 'BD',
            children: [{
              id: 9,
              label: 'CPS业务'
            },{
              id: 10,
              label: '贷款超市'
            },{
              id: 11,
              label: '甲方合作'
            }]
          }, {
            id: 6,
            label: '市场',
            children: [{
              id: 12,
              label: '应用市场'
            },
            {
              id: 13,
              label: '程序化购物'
            },
            {
              id: 14,
              label: 'ASO'
            }]
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        data5:[]
      }
    },
    mounted(){
		let sessid  = JSON.parse(localStorage.getItem("userperadmin")).sessionid;
	    this.sessionidobj = JSON.stringify({sessionid:sessid}) 
		this.creatAdminId = sessid;
    	this.getChannel(this.sessionidobj)
    },
    methods: {
      getChannel(data){
      	var _this = this;
      	_this.data5 = [];
        getChannelAttribute(data).then(res=>{
        	// console.log(res.data)
        	if (res.data.status === 0) {
              this.loading = false;
              // 一级目录
              var channelAttribute = res.data.channelAttribute;
              // 二级目录
              var channelType = res.data.channelType;
              // 三级目录
              var channelSubdivision = res.data.channelSubdivision;
              for (var i = 0; i < channelAttribute.length; i++) {
              	// 时间戳转时间
              	var updateTimestr = _this.formatDate(channelAttribute[i].updateTime);
              	// label显示的内容  id  creatAdmin创建人  updateAdmin最后操作人 updateTime修改时间
              	let id = channelAttribute[i].id;
              	let creatAdmin = channelAttribute[i].creatAdmin;
              	let higherId = channelAttribute[i].higherId;
              	let type = channelAttribute[i].type;
              	let creatAdminId = channelAttribute[i].creatAdminId;
              	let name = channelAttribute[i].name;
              	let updateAdmin = channelAttribute[i].updateAdmin;
              	let updateAdminId = channelAttribute[i].updateAdminId;
              	let updateTime = channelAttribute[i].updateTime;
              	var label = (name+'/创建:'+creatAdmin+'/最后操作:'+updateAdmin+'/修改:'+updateTimestr);
              	// 数据
              	_this.data5.push({id:id,label:label,creatAdmin:creatAdmin,higherId:higherId,type:type,creatAdminId:creatAdminId,name:name,updateAdmin:updateAdmin,updateAdminId:updateAdminId,updateTime:updateTime});
              	 _this.data5[i].children = [];
              	for (var j = 0; j < channelType.length; j++) {
              		if (channelAttribute[i].id === channelType[j].higherId) {
                         // 时间戳转时间
		              	var updateTimestr2 = _this.formatDate(channelType[j].updateTime);
		              	// label2显示的内容  id2  creatAdmin2创建人  updateAdmin2最后操作人 updateTime2修改时间
		              	let id2 = channelType[j].id;
		              	let creatAdmin2 = channelType[j].creatAdmin;
		              	let higherId2 = channelType[j].higherId;
		              	let type2 = channelType[j].type;
		              	let creatAdminId2 = channelType[j].creatAdminId;
		              	let name2 = channelType[j].name;
		              	let updateAdmin2 = channelType[j].updateAdmin;
		              	let updateAdminId2 = channelType[j].updateAdminId;
		              	let updateTime2 = channelType[j].updateTime;
		              	var label2 = (name2+'/创建:'+creatAdmin2+'/最后操作:'+updateAdmin2+'/修改:'+updateTimestr2);
		          		_this.data5[i].children.push({id:id2,label:label2,creatAdmin:creatAdmin2,higherId:higherId2,type:type2,creatAdminId:creatAdminId2,name:name2,updateAdmin:updateAdmin2,updateAdminId:updateAdminId2,updateTime:updateTime2});
	          		};
              	};
              }
              // 三级数据单独进行从新赋值
              for (var i = 0; i < _this.data5.length; i++) {
              	for (var j = 0; j < _this.data5[i].children.length; j++) {
              		_this.data5[i].children[j].children = [];
              		for (var m = 0; m < channelSubdivision.length; m++) {
              			if (_this.data5[i].children[j].id == channelSubdivision[m].higherId) {
              				// console.log(channelSubdivision[m].higherId)
              				 // 时间戳转时间
			              	var updateTimestr3 = _this.formatDate(channelSubdivision[m].updateTime);
			              	// label2显示的内容  id2  creatAdmin2创建人  updateAdmin2最后操作人 updateTime2修改时间
			              	let id3 = channelSubdivision[m].id;
			              	let creatAdmin3 = channelSubdivision[m].creatAdmin;
			              	let higherId3 = channelSubdivision[m].higherId;
			              	let type3 = channelSubdivision[m].type;
			              	let creatAdminId3 = channelSubdivision[m].creatAdminId;
			              	let name3 = channelSubdivision[m].name;
			              	let updateAdmin3 = channelSubdivision[m].updateAdmin;
			              	let updateAdminId3 = channelSubdivision[m].updateAdminId;
			              	let updateTime3 = channelSubdivision[m].updateTime;
			              	var label3 = (name3+'/创建:'+creatAdmin3+'/最后操作:'+updateAdmin3+'/修改:'+updateTimestr3);
              				_this.data5[i].children[j].children.push({id:id3,label:label3,creatAdmin:creatAdmin3,higherId:higherId3,type:type3,creatAdminId:creatAdminId3,name:name3,updateAdmin:updateAdmin3,updateAdminId:updateAdminId3,updateTime:updateTime3})
              			}
              		}
              	}
              }
              // console.log('------------')
              // console.log(_this.data5)
              _this.data4 =_this.data5
        	}
        	if (res.data.status === 1) {
        	  this.loading = false;
        	  this.$router.replace("/login");
        	}
        }).catch(res=>{
           this.$message.error('数据获取失败,请联系管理员');
        })
      },
      // 添加第一级菜单
      appendFirst(){
      	this.addInfo = {};
      	this.modeTitle = '添加'
      	this.dialogVisible = true;
      	let updateTime = (new Date()).valueOf();
      	this.addInfo = {
      		sessionid:this.creatAdminId,
      	 	creatAdminId:this.creatAdminId,
      	 	name:this.name,
      	 	higherId:0,
      	 	updateAdminId:this.creatAdminId,
      	 	updateTime:updateTime,
      	 	type:3
      	   }
      },
      append(data,number) {
      	this.addInfo = {};
      	// console.log('*******************')
      	// console.log(data)
      	// 添加时候number为0 修改时候为1
      	// console.log(number)
      	this.dialogVisible = true;
      	let updateTime = (new Date()).valueOf();
      	if (number === 0) {
      		this.modeTitle = '添加--'+data.name+'--子标签'
      		this.addInfo = {
      		sessionid:this.creatAdminId,
      	 	creatAdminId:this.creatAdminId,
      	 	name:this.name,
      	 	higherId:data.id,
      	 	updateAdminId:this.creatAdminId,
      	 	updateTime:updateTime,
      	 	type:data.type+1
      	   }
      	}
      	if (number === 1) {
      		this.modeTitle = '修改:'+data.name
      		this.addInfo = {
      		sessionid:this.creatAdminId,
      	 	creatAdminId:data.creatAdminId,
      	 	name:this.name,
      	 	higherId:data.higherId,
      	 	updateAdminId:this.creatAdminId,
      	 	updateTime:updateTime,
      	 	type:data.type,
      	 	id:data.id
      	   }
      	}
      },
     // 删除时间
      // remove(node, data) {
      // 	alert(2)
      //   const parent = node.parent;
      //   const children = parent.data.children || parent.data;
      //   const index = children.findIndex(d => d.id === data.id);
      //   children.splice(index, 1);
      // },
      // 保存添加的信息
      saveAddInfo(){
      	this.savelog = true;
      	var _this = this;
      	if (_this.addInfo.name) {
      		let params = JSON.stringify(_this.addInfo)
      		// console.log(params)
      	   saveChannelAttribute(params).then(res=>{
      	   	// console.log(res.data)
	      	   	if (res.data.status === 0) {
	      	   	  // console.log(res.data)
	      	   	  _this.savelog = false;
	      	   	  _this.dialogVisible = false;
	      	   	  _this.$message({
		          message: '保存成功',
		          type: 'success'
		        });
	      	   	_this.getChannel(_this.sessionidobj)
      	   	}
	      	   	if (res.data.status === 1) {
	      	   	  _this.savelog = false;
	      	   	  _this.dialogVisible = false;
	      	   	  _this.$router.replace("/login");
	      	   	}
      	   }).catch(res=>{
             _this.$message.error('保存失败');
           })
      	} else {
      		_this.savelog = false;
      		_this.$message.error('添加的名字不能为空');
      	}
      },
      renderContent(h, { node, data, store }) {
      	var index = node.label.indexOf('/')
        var name = node.label.substring(0, index)
        var value = node.label.substring(index)
        return (
          <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
            <span>
              <span  style='font-size:0.7rem;color:#333333'>{name}</span>
              <span  style='font-size:0.6rem;color:#999999'>{value}</span>
            </span>
            <span>
              <el-button style="font-size: 12px;" type="text" on-click={ () => this.append(data,0) }>{data.type<=4?'添加':''}</el-button>
             <el-button style="font-size: 12px;" type="text" on-click={ () => this.append(data,1) }>修改</el-button>
              <el-button style="font-size: 12px;display:none" type="text" on-click={ () => this.remove(node, data) } >删除</el-button>
            </span>
          </span>);
      } 
    }
  }

</script>
<style type="text/css" lang='scss'>
	.addfrist{
		float:right;
		margin-top:-2rem;
	}
	.tree{
		margin-top:2rem;
	}
</style>



