(function(doc, win) {
    //获取设备类型
    function deviceType() {
        var browser = {
            version: function () {
                var u = navigator.userAgent;
                var app = navigator.appVersion;
                return {
                    trident : u.indexOf('Trident') > -1,
                    presto : u.indexOf('Presto') > -1,
                    webkit : u.indexOf('AppleWebkit') > -1,
                    gecko : u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1,
                    // mobile : !!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/AppleWebKit/),
                    mobile:  u.indexOf('Mobile') > -1 || u.indexOf('mobile') > -1,
                    ios : !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/),
                    android : u.indexOf('Android') > -1 || u.indexOf('Linux') > -1,
                    iPhone : u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1,
                    iPad : u.indexOf('iPad') > -1,
                    webApp : u.indexOf('Safari') == -1,
                    wechat : u.indexOf('MicroMessenger')>-1
                };
            }(),
            language:(navigator.browserLanguge || navigator.language).toLowerCase()
        }

        if( browser.version.ios || browser.version.iPhone || browser.version.iPad ){
            if(browser.version.mobile){
                return 'ios_mobile';
            } else {
                return 'ios_pc';
            }
        }else if( browser.version.android ){
            return 'android';
        }else{
            return 'pc';
        }
    }
    var docEl = doc.documentElement,
        resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
        recalc = function() {
            var devicetype = deviceType();
            var clientWidth = docEl.clientWidth;
            var orientations = 'landscape';
            var fontSize = 16;
            if (!clientWidth) return;
            if (devicetype == 'pc' || devicetype == 'ios_pc') {
                //横屏
                if (clientWidth <= 1204) {
                    fontSize = Math.floor(clientWidth / 54);
                } else {
                    fontSize = Math.floor(clientWidth / 70);
                }
                orientations = 'horizontal';
            } else {
                fontSize = Math.floor(clientWidth / 23);
            }
            docEl.style.fontSize = fontSize + 'px';
            docEl.className = orientations;
        };
    recalc();
    if (!doc.addEventListener) return;
    win.addEventListener(resizeEvt, recalc, false);
    doc.addEventListener('DOMContentLoaded', recalc, false);
})(document, window);


