<template>
	<div class="reportCenter">
    <div  v-loading="Statrloading" element-loading-text="拼命加载中...">
  		<el-row :span="24">
  			<!-- 渠道搜索 -->
  			<el-col :span="3">
  	        <!--  <el-select  class='templeMaring'
  			    v-model="valuequdao"
  			             :filter-method="qudaoFilter"
  			             clearable
  					    filterable
  					    allow-create
  					    default-first-option
  			    placeholder="渠道">
  			    <el-option
  			      v-for="item in optionsqudao"
  			      :key="item.value"
  			      :label="item.label"
  			      :value="item.value">
  			    </el-option>
  			  </el-select> -->
          <el-select v-model="qudaoInfo.valuequdao" filterable clearable allow-create default-first-option  placeholder="渠道" class='templeMaring' @focus="focusQudao">
          <el-option
            v-for="item in qudaoInfo.optionsqudao"
            :key="item"
            :label="item"
            :value="item">
          </el-option>
    </el-select>
  			</el-col>
  			  <!-- 渠道号搜索 -->
  			<el-col :span="3">
  				<el-select  class='templeMaring' v-model="qudaoInfo.valuequdaonum"  filterable allow-create clearable default-first-option placeholder="渠道号" @focus="focusQudaonum">
  				    <el-option
  				      v-for="item in qudaoInfo.optionsqudaonum"
  				      :key="item"
  				      :label="item"
  				      :value="item">
  				    </el-option>
  				  </el-select>
  			 </el-col>
  			  <!-- 选择负责人 -->
  			 <el-col :span="3">
  				<el-select  class='templeMaring'
  				    v-model="fuzherenname"   clearable
  				    placeholder="选择负责人">
  				    <el-option
  				      v-for="item in fuzheren"
  				      :key="item.value"
  				      :label="item.label"
  				      :value="item.value">
  				    </el-option>
  				 </el-select>
  			 </el-col>
  			 <!-- 选择渠道属性 -->
  			 <el-col :span="3">
  				<el-select  class='templeMaring'
  				    v-model="qudaoshuxingname" clearable 
  				    @change="qudaoProprFilter"
  				    placeholder="选择渠道属性">
  				    <el-option
  				      v-for="item in qudaoshuxing"
  				      :key="item.value"
  				      :label="item.label"
  				      :value="item.value">
  				    </el-option>
  				</el-select>
  			</el-col>
  			<!-- 选择渠道分类 -->
  			<el-col :span="3">
  				<el-select class='templeMaring' v-model="qudaoClass" placeholder="选择渠道分类" >
  				    <el-option-group
  				      v-for="group in options4"
  				      :key="group.label"
  				      :label="group.label">
  				      <el-option
  				        v-for="item in group.options"
  				        :key="item.value"
  				        :label="item.label"
  				        :value="item.value">
  				      </el-option>
  				    </el-option-group>
  			    </el-select>
  			 </el-col>
  			<!-- 选择日期 -->
  			<el-col :span="3">
  			 <div class="block" >
  			    <el-date-picker
  			      v-model="gitTimeold"
  			      type="daterange"
  			      start-placeholder="开始日期"
  			      end-placeholder="结束日期"
  			      :default-value="newDate"
  			      unlink-panels
  			      value-format="yyyy-MM-dd"
  			      >
  			    </el-date-picker>
  			 </div>
  			</el-col>
  		</el-row>
  		<!-- 字段 -->
  		<div style="height: 1rem;"></div>
  		<el-row :span='24'>
  			<el-col :span="1"><span class="fontchange">字段:</span></el-col> 
  			<el-col :span="17" class="cheackTop">
  				<el-checkbox-group v-model="checkList">
  					<el-col :span="3">
  					   <el-checkbox label="渠道" ></el-checkbox>
  					</el-col>
  					<el-col :span="3">
  					   <el-checkbox label="渠道号"></el-checkbox>
  					</el-col>
  					<el-col :span="4">
  					   <el-checkbox label="渠道分类"></el-checkbox>
  					</el-col>
  					<el-col :span="3">
  					   <el-checkbox label="负责人"></el-checkbox>
  					</el-col>
  					<el-col :span="2">
  					    <el-checkbox label="H5"></el-checkbox>
  					</el-col>
  					<el-col :span="2">
  					   <el-checkbox label="额度"></el-checkbox>
  					</el-col> 
  				</el-checkbox-group>
  			</el-col> 
  			<el-col :span="6" class="downsharchRight">
  				<el-col :span="8" >
  				   <el-button type="info" icon="el-icon-search" size='mini' @click='search'></el-button>
  				</el-col>
  				<el-col :span="8" >
  				   <el-button type="success"  :class="iconImg"  size='mini' @click='tableTimeStyle'></el-button>
  				</el-col>
  				<el-col :span="8" >
  				   <el-button type="primary" class="fa fa-cloud-download" size='mini'></el-button>
  				</el-col>
  			</el-col>
  		</el-row>
  </div>
    <!-- 表格 -->
    <div class="table-wrapper" v-loading="Statrloading" element-loading-text="拼命加载中...">
      <el-table :data="fbsData"  style="width: 98%" border align=right size="mini" v-loading="tableloading" element-loading-text="拼命加载中..." >
                <el-table-column prop="date" label="时间" width='100'  sortable ></el-table-column>
                <!-- <el-table-column prop="name" label="时间"  sortable v-if='fontCheack.dataTime'></el-table-column> -->
                <el-table-column prop="channelName" label="渠道" v-if='fontCheack.qudao' ></el-table-column>
                <el-table-column prop="channel" label="渠道号" v-if='fontCheack.qudaoNumber'></el-table-column>
                <el-table-column prop="channelType" label="渠道分类" v-if='fontCheack.qudaoClass'></el-table-column>
                <el-table-column prop="adminName" label="负责人" v-if='fontCheack.fuzeren'></el-table-column>
                <el-table-column prop="h5Click" label="H5点击" v-if='fontCheack.H5' sortable ></el-table-column>
                <el-table-column prop="h5Register" label="H5注册" v-if='fontCheack.H5' sortable ></el-table-column>
                <el-table-column prop="activation" label="激活" v-if='fontCheack.H5' sortable ></el-table-column>
                <el-table-column prop="register" label="注册数" sortable ></el-table-column>
                <el-table-column prop="upload" label="进件数" sortable ></el-table-column>
                <el-table-column prop="account" label="开户数" sortable ></el-table-column>
                <el-table-column prop="loan" label="放款数" sortable ></el-table-column>
                <el-table-column prop="credit" label="授信总额" v-if='fontCheack.moneySize' sortable></el-table-column>
                <el-table-column prop="perCapitaCredit" label="人均批额" v-if='fontCheack.moneySize' sortable ></el-table-column>
                <el-table-column prop="firstGetPer" label="首提人数" sortable></el-table-column>
                <el-table-column prop="firstGetSum" label="首贷总额" sortable></el-table-column>
                <el-table-column prop="channelSum" label="渠道提现总额" sortable></el-table-column>
            </el-table>
            <el-pagination
                background
                @size-change="pageSizeChange"
                @current-change="currentPageChange"
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="dataSize" class="paginationWrapper">
            </el-pagination>
    </div>
   </div>
</template>

<script>
  import {getChannelParameterReg,getChannelAttribute,getChannel,getReportform} from '../../api/api.js'
  export default {
    data() {
      return {
      	// 用户sessionid
      	sessionid:'',
        Statrloading:true,
      	// 渠道分类市场BD和子信息保存便于根据选择属性显示不同信息
      	channelType:[],
      	channelSubdivision:[],
      	// 获取当前时间
      	newDate:'',
      	// 渠道
        qudaoInfo: {
          // 渠道值
          valuequdao: '',
          // 渠道信息，渠道号信息
          optionsqudao:[],
          optionsqudaonum:[],
          // 渠道号值
          valuequdaonum: '',
        }, 
        // 选择负责人
        fuzheren: [],
        fuzherenname: '',
        // 选择渠道属性
        qudaoshuxing: [{
          value: '-1',
          label: '全部'
        }],
        qudaoshuxingname: '',
        // 选择渠道分类
         options4: [],
        qudaoClass: '',
        // 日期
        gitTimeold: '',
        gitTimeNew:{},
        // 字段
       checkList: ["渠道","渠道号","渠道分类","负责人","H5","额度"],
       // 表格显示时间样式
       // iconImg:'fa fa-times-circle',
       iconImg:'fa fa-sun-o',
       // 命名一个变量来判断icon是第几个
       iconImgIndex:1,
       // 搜索获取的数据
       fbsData:[],
       tableloading:false,
       // 记录字段勾选时候搜索显示隐藏
       fontCheack:{
         qudao: false,
         qudaoNumber: false,
         qudaoClass: false,
         fuzeren: false,
         H5: false,
         moneySize: false
       },
       // 搜索发送的数据
       secrchSetData:{},
       // 分页信息
        currentPage: 1,
        pageSizes: [10,20,50],
        dataSize: 0,
        pagesize: 10,
        first:0,
        // 日 月 条数
        dataTimeDay: 0,
        dataTimeMoth: 0,
        baobiaoSearchIndex:0
      }
    },
    methods:{
    	// 页面加载时获取搜索框中要显示的内容
    	sharchInfoSelect(sessionid){
        var statusqudaoInfo = false;
        var statusclass = false;
    		var _this = this;
    		let data = {
    			sessionid:sessionid
    		};
    		let dataStr = JSON.stringify(data);
       //getChannelParameterReg,saveChannelAttribute
           // 获取渠道渠道号接口,获取负责人信息接口数据
           getChannelParameterReg(dataStr).then(res=>{
	           	console.log(res.data)
	           	if (res.data.status === 0) {
	               for (var i = 0; i < res.data.adminIdList.length; i++) {
	                  let id = res.data.adminIdList[i];
                    let name = res.data.adminIdList[i].split(',')[1];
                    _this.fuzheren.push({value:id,label:name});
                    _this.qudaoInfo.optionsqudao = res.data.channelNameList;
                    _this.qudaoInfo.optionsqudaonum = res.data.channelNoList;
                    statusqudaoInfo = true;
	               }
	           	};
	           	if (res.data.status === 1) {
	               _this.$router.replace("/login");
	           	};
           });
           // 获取渠道属性属性分类信息
           getChannelAttribute(dataStr).then(res=>{
           	    console.log(res.data)
                if (res.data.status === 0) {
                	for (var j = 0; j < res.data.channelAttribute.length; j++) {
                    // let id = res.data.channelAttribute[j].higherId+','+res.data.channelAttribute[j].id;
                		let id = res.data.channelAttribute[j].id;
                		let name = res.data.channelAttribute[j].name;
                		_this.qudaoshuxing.push({value:id,label:name})
                	}
	               _this.channelType = res.data.channelType;
	               _this.channelSubdivision = res.data.channelSubdivision;
                 statusclass = true;
	           	};
	           	if (res.data.status === 1) {
	               _this.$router.replace("/login");
	           	};
           })
         var trime = setInterval(function(){
              _this.baobiaoSearchIndex++;
              if(statusqudaoInfo&& statusclass) {
                _this.Statrloading = false;
                clearInterval(trime);
                _this.baobiaoSearchIndex = 0;
                _this.search();
              } else {
                 if (_this.baobiaoSearchIndex<2) {
                    _this.sharchInfoSelect(this.sessionid);
                 } else {
                   // _this.$message.error('数据获取失败,请联系管理员');
                   clearInterval(trime);
                   _this.baobiaoSearchIndex = 4;
                 }
                _this.Statrloading = true;
               
              }
           }, 2000)   
    	},
    	// 渠道属性过滤根据选择显示渠道分类
    	qudaoProprFilter(val){
        // var val = parseInt(val.split(",")[1]);
         this.qudaoClass = '';
         this.options4 = [{options:[{value: '-1,-1',label: '全部'}]}];
    		 // this.options4 = [{options:[{value: '-1',label: '全部'}]}];
    		for (var i = 0; i < this.channelType.length; i++) {
    			if(val === this.channelType[i].higherId){
    				let id = this.channelType[i].id;
    				let label = this.channelType[i].name;
                    this.options4.push({label:label,id:id})
    			}
    			for (var j = 1; j < this.options4.length; j++) {
    				 this.options4[j].options = [];
    				 for (var m = 0; m < this.channelSubdivision.length; m++) {
                         if (this.options4[j].id === this.channelSubdivision[m].higherId) {
                          this.options4[j].options.push({value:this.channelSubdivision[m].higherId+','+this.channelSubdivision[m].id,label:this.channelSubdivision[m].name})
                         	// this.options4[j].options.push({value:this.channelSubdivision[m].id,label:this.channelSubdivision[m].name})
                         }
    				 }
    			}
    		}
    	},
    	findTime(){
    		var myDate = new Date();
			//获取当前年
			var year=myDate.getFullYear();
			//获取当前月
			var month=myDate.getMonth()+1;
			//获取当前日
			var date=myDate.getDate(); 
			// var h=myDate.getHours();       //获取当前小时数(0-23)
			// var m=myDate.getMinutes();     //获取当前分钟数(0-59)
			// var s=myDate.getSeconds();  

			this.newDate = year+'-'+month+'-'+date;
    	},
    	tableTimeStyle(){
           this.iconImgIndex++;
           // if (this.iconImgIndex<=3) {
           //   if (this.iconImgIndex === 2) {
           //   	this.iconImg = 'fa fa-sun-o'
           //   }
           //   if (this.iconImgIndex ===3) {
           //   	this.iconImg = 'fa fa-moon-o'
           //   }
           // }else {
           // 	 this.iconImgIndex = 1;
           // 	 this.iconImg = 'fa fa-times-circle'
           // }
           if (this.iconImgIndex<=2) {
             if (this.iconImgIndex === 2) {
              this.iconImg = 'fa fa-moon-o'
             }
           }else {
             this.iconImgIndex = 1;
             this.iconImg = 'fa fa-sun-o'
           }
           this.currentPage = 1;
           this.first = 0;
           this.search();
           
    	},
      // 选择页发生改变
      pageSizeChange(res){
        this.pagesize = res;
        this.first = 1;
        this.pagesize
        this.search();

      },
      // 当前页发生改变
      currentPageChange(res){
        this.currentPage = res;
        this.first = 1;
        this.search();
      },
      // 搜索事件
      search(){
           this.tableloading = true;
           // 勾选状态是否显示进行初始化
           this.fontCheack.qudao = false;
           this.fontCheack.qudaoNumber = false;
           this.fontCheack.qudaoClass = false;
           this.fontCheack.fuzeren = false;
           this.fontCheack.H5 = false;
           this.fontCheack.moneySize = false;
        // 判断是否选择了时间区间
         if (this.gitTimeold) {
            // this.gitTimeNew.satrt = this.dataToMs(this.gitTimeold[0]);
            // this.gitTimeNew.end = this.dataToMs(this.gitTimeold[1]);
            this.gitTimeNew.satrt = this.gitTimeold[0];
            this.gitTimeNew.end = this.gitTimeold[1];
         } else {
           this.gitTimeNew.satrt = '';
           this.gitTimeNew.end = '';
         }
         // 字段发送值
         this.secrchSetData = {
            sessionid:this.sessionid,
            channelName: this.qudaoInfo.valuequdao ?this.qudaoInfo.valuequdao:'',
            channelNo: this.qudaoInfo.valuequdaonum ?this.qudaoInfo.valuequdaonum:'',
            adminId:this.fuzherenname?this.fuzherenname.split(',')[0]:'',
            adminName:this.fuzherenname?this.fuzherenname.split(',')[1]:'',
            attribute: this.qudaoshuxingname && this.qudaoshuxingname != '-1'? this.qudaoshuxingname : '',
            type: this.qudaoClass && this.qudaoClass !='-1,-1' ? this.qudaoClass.split(',')[0]: '',
            subdivision: this.qudaoClass && this.qudaoClass !='-1,-1' ? this.qudaoClass.split(',')[1]: '',
            minDate:this.gitTimeNew.satrt,
            maxDate:this.gitTimeNew.end,
            dateType: this.iconImgIndex?this.iconImgIndex:'',
            pageId: this.currentPage-1,
            pageSize: this.pagesize,
            first: this.first
         } 
         var _this = this;
         var searchSetData = JSON.stringify(this.secrchSetData);
         console.log(searchSetData)
         getReportform(searchSetData).then(res=>{
          console.log(res.data)
           if (res.data.status === 0) {
             if (this.secrchSetData.dateType === 1) {
                _this.fbsData = res.data.reportforms_day;
             };
             if (this.secrchSetData.dateType === 2) {
                _this.fbsData = res.data.reportforms_month;
             }
             if (!res.data.listSize) {
                _this.dataSize = _this.dataSize
             } else {
               if (this.secrchSetData.dateType === 1) {
                  _this.dataTimeDay = parseInt(res.data.listSize);
                  _this.dataSize = _this.dataTimeDay;
               }
               if (this.secrchSetData.dateType === 2) {
                  _this.dataTimeMoth = parseInt(res.data.listSize);
                  _this.dataSize = _this.dataTimeMoth;
               }
               // _this.dataSize = parseInt(res.data.listSize);
             }
             _this.tableloading = false;

           };
           if (res.data.status === 1) {
             _this.$router.replace("/login");
           }
         }).catch(res=>{
           _this.$message.error('数据搜索失败');
           _this.tableloading = false;
         })
         // 搜索时候根据字段勾选的显示本列(此处代码用着搜索成功时候)
         console.log(this.checkList)
         if (this.checkList.length>0) {
            for (var i = 0; i < this.checkList.length; i++) {
                if (this.checkList[i] === '渠道') {
                  this.fontCheack.qudao = true;
                } else if (this.checkList[i] === '渠道号') {
                  this.fontCheack.qudaoNumber = true;
                } else if (this.checkList[i] === '渠道分类') {
                  this.fontCheack.qudaoClass = true;
                } else if (this.checkList[i] === '负责人') {
                  this.fontCheack.fuzeren = true;
                } else if (this.checkList[i] === 'H5') {
                  this.fontCheack.H5 = true;
                } else if (this.checkList[i] === '额度') {
                  this.fontCheack.moneySize = true;
                }
            }
         }
        // console.log(this.fuzherenname)
        // console.log(this.qudaoshuxingname)
        // console.log(this.qudaoClass)
        // console.log(this.gitTimeNew)
        // console.log(this.checkList)
        // console.log(this.iconImgIndex)
        console.log(this.secrchSetData)
      },
      focusQudao(){
        this.qudaoInfo.valuequdao = '';
      },
      focusQudaonum(){
        this.qudaoInfo.valuequdaonum = '';
      }
    },
    mounted(){
    	// 获取用户sessionid
    	var userperadmin = localStorage.getItem("userperadmin");
			this.sessionid = JSON.parse(userperadmin).sessionid;
    	// 使日历对应当前时间日期
    	this.findTime();
    	// 获取搜索选择的信息负责人和选择渠道属性选择渠道分类以及渠道和渠道号
    	this.sharchInfoSelect(this.sessionid);
    }
  }
</script>
<style scoped lang="scss">
	.templeMaring{
		margin-right: 1rem;
	}
	.fontchange{
		font-size: 0.7rem;
	}
	.cheackTop{
		margin-top: 0.2rem;
	}
	.fa {
		font-size: 0.6rem;
	}
  .table-wrapper{
    margin-top:2rem;
  }
</style>


