import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
var state = {
	sessionid:"",
	level:null
};
var mutations = {
	userId(state, id) {
	    state.sessionid = id
    },
    userpermission(state, level) {
	    state.level = level
    }
};
export default new Vuex.Store({
    state,
    mutations
});


