import axios from 'axios';
// import qs from 'qs';
let base = 'http://10.68.6.168:8080/operate/Index/';

// let base = 'http://120.55.184.17:8080/operate/Index/';
// 登录请求
export const requestLogin = params => { return axios.post(`${base}login`, params)};
// 用户中心
export const getAdminList = params => { return axios.post(`${base}getAdmin`,params)};
export const adduser = params => { return axios.post(`${base}addAdmin`,params)};
export const addAdmin = params => { return axios.post(`${base}addAdmin`,params)};
export const AdminParameter = params => { return axios.post(`${base}AdminParameter`,params)};
// 渠道中心
export const companyListReg = params => { return axios.post(`${base}getProxyNameList`,params)}; //获取全部公司
export const channelListReg = params => { return axios.post(`${base}getProxy`,params)}; // 获取渠道商列表信息
export const updateChannelReg = params => { return axios.post(`${base}addProxy`,params)}; // 新增|编辑渠道商列表
export const getChannelParameterReg = params => { return axios.post(`${base}getChannelParameter`,params)}; //获取渠道查询条件列表
export const getChannelReg = params => { return axios.post(`${base}getChannel`,params)}; // 获取渠道列表信息
export const getChannelValueReg = params => { return axios.post(`${base}getChannelValue`,params)}; // 获取新增|编辑渠道时下拉选择列表数据
export const getChannelPermissionReg = params => { return axios.post(`${base}getChannelPermission`,params)}; // 获取账户权限
export const addChannelReg = params => { return axios.post(`${base}addChannel`,params)}; // 保存渠道信息
export const getRewardReg = params => { return axios.post(`${base}getReward`,params)}; // 保存渠道信息
export const checkChannelNoReg = params => { return axios.post(`${base}checkChannelNo`,params)}; // 校验渠道号
export const checkMobileNoReg = params => { return axios.post(`${base}checkMobileNo`,params)}; // 校验手机号码
export const checkEmailReg = params => { return axios.post(`${base}checkEmail`,params)}; // 校验手机号码
// 渠道分配
export const getChannelAttribute = params => { return axios.post(`${base}getChannelAttribute`,params)};
// 渠道分配保存修改接口
export const saveChannelAttribute = params => { return axios.post(`${base}saveChannelAttribute`,params)};
// 渠道号渠道信息
export const getChannel = params => { return axios.post(`${base}getChannel`,params)};
// 渠道中心查询接口
export const getReportform = params => { return axios.post(`${base}getReportform`,params)};




