<template>
  <div class="login">
       <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container" style="margin-bottom: 0px" v-show="forgetPws">
        <h3 class="title">渠道代理系统登录</h3>
        <el-form-item prop="email">
          <el-input type="text" v-model="ruleForm2.email" auto-complete="off" placeholder="账号"></el-input>
        </el-form-item>
        <el-form-item prop="pwd">
          <el-input type="password" v-model="ruleForm2.pwd" auto-complete="off" placeholder="密码"></el-input>
        </el-form-item>
        <el-checkbox v-model="checked" checked class="remember">记住账号</el-checkbox>
        <el-button class="forgetPws" @click="forgetPwsNum">忘记密码?</el-button>
        <el-form-item style="width:100%;">
          <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
        </el-form-item>
      </el-form> 
      <el-form  label-position="left" label-width="0px" class="demo-ruleForm login-container" style="margin-bottom: 0px" v-show="!forgetPws">
        <h3 class="title">忘记密码</h3>
          <el-input type="text" v-model="uesrEmail" auto-complete="off" placeholder="账号"></el-input>
        <el-button type="text" @click="returnLogin" style="margin-left: 80%">返回登录</el-button>
        <el-form-item style="width:100%;">
          <el-button type="primary" style="width:100%;" @click.native.prevent="sendSet">提交</el-button>
          <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
        </el-form-item>
        <span class="titleInfo" ref='isTrueEmail'></span>
      </el-form>
  </div>
</template>

<script>
  import {requestLogin} from '../api/api'
  export default {
    data() {
      return {
        // 是否忘记密码状态
        forgetPws: true,
        uesrEmail: '',
        logining: false,
        ruleForm2: {
          email: "",
          pwd: ""
        },
        rules2: {
          email: [
            { required: true, message: '请输入账号', trigger: 'blur' },
          ],
          pwd: [
            { required: true, message: '请输入密码', trigger: 'blur' },
          ]
        },
        checked: true
      };
    },
    mounted(){
      let userInfoemail = localStorage.getItem("userInfo");
      console.log(userInfoemail);
      if (userInfoemail) {
        this.ruleForm2.email = userInfoemail;
        this.uesrEmail = userInfoemail;
      } else {
        this.ruleForm2 ={email:"",pwd:""};
        this.uesrEmail = '';
      }
    },
    methods: {
    	handleSubmit2:function(){
        var _this = this;
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            this.logining = true;
            let ruleForm2 = JSON.stringify(this.ruleForm2);
            requestLogin(ruleForm2).then((res)=>{
                console.log(res.data)
                if (res.data.status === 0) {
                  if (_this.checked) {
                     let userInfo = localStorage.setItem("userInfo", this.ruleForm2.email);
                  } else {
                    localStorage.setItem("userInfo", "");
                    sessionStorage.setItem("userInfosession", this.ruleForm2.email)
                  } 
                   localStorage.setItem("userId", res.data.sessionid);
                   localStorage.setItem("userpermission", res.data.admin.level);
                   var userperadmin = JSON.stringify(res.data);
                   localStorage.setItem("userperadmin",userperadmin);
                    // _this.$store.commit("userId",res.data.sessionid);
                    // _this.$store.commit("userpermission",res.data.admin.level);
                    _this.logining = false;
                   _this.$router.push("/Channelmanagement");
                } else {
                  console.log(res.data.statusMsg)
                  _this.$message.error(res.data.statusMsg);
                  _this.logining = false;
                }
            }).catch(res=>{
               _this.$message.error('登录失败,请联系管理员');
            })
            // this.$router.push("/Homepage")
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    	},
      forgetPwsNum(){
        this.forgetPws = !this.forgetPws;
      },
      returnLogin(){
        this.forgetPws = !this.forgetPws;
      },
      sendSet(){
        var _this = this;
        if (_this.uesrEmail) {
          var reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/; 
          if (!reg.test(_this.uesrEmail)) {
            _this.$refs.isTrueEmail.innerText = "邮箱格式不正确";
            setTimeout(function(){
              _this.$refs.isTrueEmail.innerText = " ";
            },1500)
          } else {
            // 进行数据请求
            // _this.forgetPws = !_this.forgetPws;
          }
        } else {
          _this.$refs.isTrueEmail.innerText = "用户名不能为空";
          setTimeout(function(){
              _this.$refs.isTrueEmail.innerText = " ";
            },1500)
        }
      }
    }
  }

</script>
<style lang="scss" scoped>
  .login{
    width:100%;
    height: 100%;
    background-image: url("../assets/images/loginbg.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
  }
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 0.32rem;
    border-radius: 0.32rem;
    -moz-border-radius: 0.32rem;
    background-clip: padding-box;
    margin: 9.25rem auto;
    width: 16.25rem;
    padding: 2.19rem 2.19rem 0.94rem 2.19rem;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 1.56rem #cac6c6;
    .title {
      margin: 0px auto 2.5rem auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 2.19rem 0px;
    }
    .el-row{
      min-width: 56.25rem;
    }
    .forgetPws{
      float: right;
      padding:0px;
      border:1px solid #fff;
      color: #409EFF;
    }
    .titleInfo{
     display: inline-block;
     font-size: 0.6rem;
     color:red;
     margin-left:35%;
    }
  }
</style>


