import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home'
import Login from '../views/Login'
import Channelmanagement from '../views/ChannelCenter/Channelmanagement.vue'
// import Companys from '../views/ChannelCenter/Companys.vue'
// import Channels from '../views/ChannelCenter/Channels.vue'
import Channelclass from '../views/ChannelCenter/Channelclass.vue'
import ReportCenter from '../views/ReportCenter/ReportCenter.vue'
import UserControl from '../views/UserControl/UserControl.vue'


Vue.use(Router)

export default new Router({
  routes: [
     {
        path: '/Login',
        component: Login,
        name: '',
        hidden: true
    },
     { path: "/", redirect: '/Login',hidden: true },
    {
       path: '/',
       component: Home,
       name: '渠道中心',
       iconCls: 'el-icon-message',//图标样式class
       children: [
          { path: '/Channelmanagement', component: Channelmanagement, name: '渠道商管理'},
          // { path: '/companys', component: Companys, name: '渠道商'},
          // { path: '/channles', component: Channels, name: '渠道管理'},
          { path: '/Channelclass', component: Channelclass, name: '渠道分类配置'}
       ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-date',
        leaf: true,//只有一个节点
        children: [
            { path: '/ReportCenter', component: ReportCenter, name: '报表中心' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-date',
        leaf: true,//只有一个节点
        children: [
            { path: '/UserControl', component: UserControl, name: '用户管理' }
        ]
    }
  ]
})



